


.switch-flex {
    margin-right: 60px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
   
    @include tablet{
      right: 55px;
    }
  }
  

  
  .theme__switch {
    display: inline-block;
    height: 28px;
    position: relative;
    width: 54px;
  }
  
  .theme__switch input {
    display: none;
  }
  
  .slider {
    background:#8c8c8c;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: 0.4s;

  }
  
  .slider::before {
    background:#ffffff;
    bottom: 4px;
    content: "";
    height: 20px;
    left: 4px;
    position: absolute;
    transition: 0.4s;
    width: 20px;
  }

  
  input:checked + .slider {
    background:  #ff6b08;
  }
  
  input:checked + .slider::before {
    @media screen and (min-width:769px){
      transform: translateX(16px);
    }

  }
  
  .slider.round {
margin-right: 10px;
    border-radius: 34px;
    @media screen and (max-width:768px){
      width: 28px;
    }
  }
  
  .slider.round::before {
    border-radius: 50%;
  }
 
  
  .icon__dark{
    color: var(--accent-color);
  }
